import React, { useRef, useContext, useState, useEffect } from "react";
import { SlideMenu } from "primereact/slidemenu";
import { Button } from "primereact/button";
import { FormattedMessage } from "react-intl";
import { langContext } from "../context/langContext";

const Header = () => {
  const [actualState, changeCheckState] = useState(true);
  const idioma = useContext(langContext);

  const lang = localStorage.getItem("lang");

  const handleCheckbox = (e) => {
    changeCheckState(e.target.checked);
    idioma.establecerLenguaje(actualState);
  };

  useEffect(() => {
    if (lang) {
      if (lang === "es-ES") {
        changeCheckState(false);
      } else if (lang === "en-US") {
        changeCheckState(true);
      } else {
        changeCheckState(true);
      }
    }
  }, []);

  const menu = useRef(null);

  const items = [
    {
      label: <FormattedMessage id="menu.company" defaultMessage="Our Company" />,
    },
    {
      label: <FormattedMessage id="menu.services" defaultMessage="Our Services"/>,
    },
    {
      label: <FormattedMessage id="menu.contact" defaultMessage="Contact" />,
    },
    {
      label: <FormattedMessage id="menu.technologies" defaultMessage="Technologies"/>,
    },
    {
      label: <FormattedMessage id="menu.launchApp" defaultMessage="Launch App" />,
      icon: "pi pi-arrow-up-right",
    },
  ];

  return (
    <header className="z-1">
      <div className="flex flex-row pHorizontal lala boxHeader">
        <div className="col-10 md:col-2 lg:col-3 font-bold text-white flex align-items-center boxLogo lg:justify-content-start">
          <img
            className="objectFit boxLogoImg "
            src={require("../assets/img/logoHeader.png")}
            alt=""
          />
        </div>
        <div className="col-2 md:col-10 lg:col-9 font-bold text-white flex justify-content-end md:justify-content-end md:justify-content-around align-items-center nav lg:px-3 textHeader">
          <div className="font-light cursor-pointer hidden md:block mx-2">
            <FormattedMessage id="menu.company" defaultMessage="Our Company" />
          </div>
          <div className="font-light cursor-pointer hidden md:block mx-2">
            <FormattedMessage id="menu.services" defaultMessage="Our Services"/>
          </div>
          <div className="font-light cursor-pointer hidden md:block mx-2">
            <FormattedMessage id="menu.contact" defaultMessage="Contact" />
          </div>
          <div className="font-light cursor-pointer hidden md:block mx-2">
            <FormattedMessage id="menu.technologies" defaultMessage="Technologies"/>
          </div>

          <button className="button-85 hidden md:block ml-2">
            <FormattedMessage id="menu.launchApp" defaultMessage="Launch App" />
          </button>

          <div class="checkbox-con">
            <input
              class="cursor-pointer"
              id="checkbox"
              checked={actualState}
              onChange={handleCheckbox}
              type="checkbox"
            />
          </div>

          {/* <button onClick={() => idioma.establecerLenguaje("en-US")} className="button-85 hidden md:block ml-2" role="button ">
            <FormattedMessage
              id="menu.launchApp"
              defaultMessage="Launch App"
            />
          </button> */}

          <div className="block md:hidden">
            <SlideMenu
              ref={menu}
              model={items}
              popup
              viewportHeight={220}
              menuWidth={175}
            ></SlideMenu>
            <Button
              type="button"
              icon="pi pi-bars"
              onClick={(event) => menu.current.toggle(event)}
            ></Button>
          </div>
        </div>
      </div>
      <div className="borderGradient"></div>
    </header>
  );
};

export default Header;
