import React from "react";
import Lottie from "react-lottie";
import animationData from "../assets/lottie/Balun.json";
import { FormattedMessage } from "react-intl";

const Home = () => {
  const defaultOptions = {
    loop: true,
    autoplay: true,
    animationData: animationData,
    rendererSettings: {
      preserveAspectRatio: "xMidYMid slice",
    },
  };
  return (
    <div className="pHorizontal pHorizontalMovil">
      <div className="grid relative contenedor ">
        {/* First section  */}
        <div className="col-12 md:col-8 flex flex-column heightTitle">
          <img
            className="objectFit absolute imgSphere"
            src={require("../assets/img/mediaSphere.png")}
            alt=""
          />
          <div className="flex xl:align-items-start">
            <div className="flex flex-column">
              <div className="w-100 z-2 text-white boxTitle font-medium">
                <FormattedMessage
                  id="init.textOne"
                  defaultMessage="Now upgrading your company using new technologies is a priority."
                />
              </div>
              <div className="w-100 z-2 text-white-alpha-70 boxDescription font-normal">
                <FormattedMessage
                  id="init.textTwo"
                  defaultMessage="The use of the Blockchain, the Metaverse and the Cryptocurrencies is today, not tomorrow."
                />
              </div>
              <div className="z-2 grid boxButtonOne">
                <div className="boxButtonOneHeight">
                  <button className="buttonContact border-none mr-4 cursor-pointer">
                    <span className="buttonContact-content buttonSectionOne font-semibold">
                      <FormattedMessage
                        id="init.buttonOne"
                        defaultMessage="Contact Us"
                      />
                    </span>
                  </button>
                </div>
                <div className="boxButtonOneHeight">
                  <button className="hidden md:block buttonLearn cursor-pointer border-none">
                    <span className="buttonSectionOne font-semibold flex justify-content-center align-items-center">
                      <FormattedMessage
                        id="init.buttonTwo"
                        defaultMessage="Learn More"
                      />
                    </span>
                  </button>
                  <button className="block md:hidden buttonLearn arrow cursor-pointer border-none">
                    <span className="buttonSectionOne font-semibold flex justify-content-center align-items-center">
                      <i className="pi pi-arrow-down" style={{'fontSize': '35px'}}></i>
                    </span>
                  </button>
                </div>
              </div>

            </div>
          </div>
        </div>
        <div className="col-12 md:col-4">
          <div className="w-full overflo lg:h-screen xl:h-full  lg:py-0 xl:py-8">
            {/* Triangle */}
            <img
              className="objectFit z-1 relative md:absolute xl:relative boxTriangle"
              src={require("../assets/img/triangle.png")}
              alt=""
            />
            {/* Radiance */}
            <img
              className="objectFit relative md:absolute xl:relative z-0  boxRadiance"
              src={require("../assets/img/radiance.png")}
              alt=""
            />
          </div>
        </div>
      </div>

      {/* End First section  */}

      {/* Second section*/}
      <div className="z-1 relative h-screen ">
        <div className="boxTitleSecond flex justify-content-center font-bold">
          Xolarix
        </div>
      </div>
      {/* End Second section*/}
    </div>
  );
};

export default Home;
